import {
  Button,
  EmailInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import React, { useLayoutEffect } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import { BASE_URL, checkReponse } from "../components/utils/burger-api";
import { useForm } from "../hooks/useForm";
import { useAppSelector } from "../hooks/useRedux";
import { ILocationState } from "../services/types/data";

import styles from "./page.module.css";

export function ForgotPassFirstPage() {
  const { isAuthorized, isLoading } = useAppSelector((state) => state.auth);
  const history = useHistory();
  const location = useLocation<ILocationState>();

  const { values, handleChange } = useForm({
    email: "",
  });

  useLayoutEffect(() => {
    if (isAuthorized && !isLoading) {
      history.replace(location?.state?.from || "/");
    }
  }, [history, isAuthorized, isLoading, location?.state?.from]);

  const onSubmitForm = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    fetch(`${BASE_URL}/password-reset`, {
      method: "POST", //  'PUT'
      body: JSON.stringify({
        email: values.email,
      }),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then(checkReponse)
      .then((data) => {
        if (data.success)
          history.replace("/reset-password", { from: "/forgot-password" });
      })
      .catch((error) => console.log("error", error));
  };

  return (
    <div className={styles.loginWrapper}>
      <form className={styles.form} onSubmit={onSubmitForm}>
        <label className={`text text_type_main-medium ${styles.label}`}>
           
        </label>
        <div className={styles.inputs}>
          <EmailInput
            onChange={handleChange}
            value={values.email}
            name={"email"}
          />
        </div>
        <Button type="primary" size="medium">
          
        </Button>
        <div className={styles.text_under_the_form}>
          {/*  span  <Link></Link> */}
          <label className={`text text_type_main-default ${styles.text}`}>
             ?{" "}
            <Link to="/login" className={styles.text_link}>
              
            </Link>
          </label>
        </div>
      </form>
    </div>
  );
}
