// rootReducer.js
import {
  TWSActions,
  WS_CLEAR_ORDERS,
  WS_CONNECTION_CLOSED,
  WS_CONNECTION_ERROR,
  WS_CONNECTION_SUCCESS,
  WS_GET_ORDERS,
} from "../actions/wsActionTypes";
import { TOrder } from "../types/data";

export type TWS = {
  wsConnected: boolean;
  orders: Array<TOrder>;
  error: undefined | string;
  total: number;
  totalToday: number;
};

const initialState: TWS = {
  wsConnected: false,
  orders: [],
  error: undefined,
  total: 0,
  totalToday: 0,
};

//    WebSocket
export const wsReducer = (state = initialState, action: TWSActions) => {
  switch (action.type) {
    //      WS_CONNECTION_SUCCESS
    //   wsConnected   true
    case WS_CONNECTION_SUCCESS:
      return {
        ...state,
        error: undefined,
        wsConnected: true,
      };

    //      WS_CONNECTION_ERROR
    //   wsConnected   false     action.payload
    case WS_CONNECTION_ERROR:
      return {
        ...state,
        error: action.payload,
        wsConnected: false,
      };

    //      WS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case WS_CONNECTION_CLOSED:
      return {
        ...state,
        error: undefined,
        wsConnected: false,
      };

    //      WS_GET_MESSAGE
    //  ,     
    //  orders  ,    
    case WS_GET_ORDERS:
      return {
        ...state,
        error: undefined,
        orders: action.payload.orders,
        total: action.payload.total,
        totalToday: action.payload.totalToday,
      };
    //  
    case WS_CLEAR_ORDERS:
      return {
        ...state,
        orders: [],
        total: 0,
        totalToday: 0,
      };
    default:
      return state;
  }
};
